body {
  font-family: 'OpenSans', sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.header {
  padding: 30px 0 30px 0;
  width: 100%;
  position: fixed;
  z-index: 50;
  box-shadow: 0 6px 4px -4px rgba(0, 0, 0, 0.2);
}

.header:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 2;
}

.header__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__body:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 2;
}

.header__toggle {
  display: none;
}

.header__logo {
  font-weight: 700;
  text-decoration: none;
  z-index: 3;
  font-size: 25px;
  padding-left: 10px;
  color: #000;
}

.header__nav {
  display: block;
  font-weight: 700;
  z-index: 3;
}

.header__nav-list {
  display: flex;
  list-style: none;
}

.header__nav-item {
  margin: 0px 0px 0px 30px;
  list-style: none;
  position: relative;
}

.header__nav-link {
  color: #000;
  font-size: 18px;
  text-decoration: none;
  padding: 8px;
  white-space: nowrap;
  transition: all 0.3s ease 0s;
  letter-spacing: 1px;
}

.header__nav-link:hover {
  color: #fff;
  background: #00897b;
}

.header__nav-link.active {
  border-bottom: 2px solid #00897b;
}

.btn-transparent,
.btn-transparent-sm {
  display: inline-block;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 16px;
  padding: 26px 35px;
  border: 3px solid #fff;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  line-height: 1.1;
  min-width: 220px;
  text-align: center;
  letter-spacing: 2px;
  transition: all 0.3s ease 0s;
  white-space: nowrap;
}

.btn-transparent-sm {
  border: 3px solid #000;
  padding: 15px 15px;
  min-width: auto;
}

.btn-transparent-sm:hover {
  padding: 15px 15px;
}

.btn-transparent:hover,
.btn-transparent-sm:hover {
  background-color: #fff;
  color: #000;
}

.btn-green {
  display: inline-block;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 16px;
  padding: 26px 35px;
  background-color: #00897b;
  border: 3px solid #00897b;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  line-height: 1.1;
  min-width: 220px;
  text-align: center;
  letter-spacing: 2px;
  transition: all 0.3s ease 0s;
  white-space: nowrap;
}

.btn-green:hover {
  background-color: transparent;
  color: #fff;
}

.title {
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.subtitle {
  font-size: 15px;
  font-weight: 600;
  line-height: 1.5;
  text-align: center;
}

.center {
  background: url("../img/center/center-bg.jpg") no-repeat;
  background-size: cover;
  width: 100%;
  padding-top: 180px;
  height: 655px;
}

.center__body {
  max-width: 860px;
  margin: 0 auto;
}

.center__title {
