  margin-bottom: 30px;
  letter-spacing: 3px;
  text-align: center;
}

.center__subtitle {
  color: #fff;
  font-size: 22px;
  text-align: center;
  margin-bottom: 80px;
  letter-spacing: 2px;
}

.center__btns {
  display: flex;
  justify-content: center;
  margin-bottom: 98px;
}

.center__btns a {
  margin: 0 15px;
}

.center__go-down {
  display: block;
  text-align: center;
}

.center__go-down a {
  font-size: 22px;
  margin-left: 5px;
  color: #fff;
}

.about-us {
  padding: 70px 0 40px 0;
}

.about-us__row-about-info {
  max-width: 630px;
  margin: 0 auto 100px auto;
}

.about-us__row-about-list {
  text-align: center;
}

.about-us__about-list-cnt {
  margin-bottom: 30px;
}

.about-us__about-list-img {
  margin-bottom: 10px;
}

.about-us__about-list-title {
  font-size: 20px;
