  background: #f0f1f1;
  box-shadow: 0 6px 4px -4px rgba(0, 0, 0, 0.2);
}

.pricing-plan__row-pricing-plan-info {
  max-width: 630px;
  margin: 0 auto 100px auto;
}

.pricing-plan__card {
  background: #fff;
