}

.pricing-plan__card-top {
  width: 100%;
  padding: 30px 0;
  background: #3949ab;
  position: relative;
  z-index: 1;
}

.pricing-plan__card-top:after {
  background: inherit;
  left: 0;
  right: 1px;
  bottom: 0;
  content: '';
  display: block;
