  padding: 0 95px;
  display: block;
  color: #fff;
}

.pricing-plan__card-per-month span:first-child {
  font-size: 50px;
  font-weight: 400;
  position: relative;
}

.pricing-plan__card-per-month span:first-child:after {
  content: '$';
  font-size: 25px;
  position: absolute;
  left: -15px;
