@media (max-width: 991.98px) {
  .header {
    padding: 10px 10px 10px 10px;
    width: 100%;
    position: fixed;
  }

  .header__nav {
    position: fixed;
    overflow: auto;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 1;
    padding-top: 48px;
    transition: all 0.3s ease 0s;
  }

  .header__nav.active {
    top: 0;
  }

  html.lock,
  body.lock {
    overflow: hidden;
  }

  .header__nav-item {
    width: 100%;
    margin: 0;
    border-bottom: 1px solid #c0c0c0;
  }

  .header__nav-link {
    display: block;
    padding: 10px 15px;
  }

  .header__nav-link.active {
    border-bottom: none;
  }

  .header__nav-list {
    display: block;
    padding: 0;
    margin: 0;
  }

  .header__logo {
    font-size: 20px;
    position: relative;
    z-index: 3;
    padding-left: 5px;
  }

  .header__toggle {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    z-index: 3;
  }

  .header__toggle:hover {
    cursor: pointer;
  }

  .header__toggle:before,
  .header__toggle:after {
    content: '';
    background-color: #000;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .header__toggle:before {
    top: 0;
  }

  .header__toggle:after {
    bottom: 0;
  }

  .header__toggle.active:before {
    content: '';
    transform: rotate(45deg);
    top: 9px;
  }

  .header__toggle.active:after {
    content: '';
    transform: rotate(-45deg);
    bottom: 9px;
  }

